import JSBI from "jsbi";
import { CurrencyAmount, ChainId, Token } from "../../sdk-core/src";
import { TransactionResponse } from "@ethersproject/providers";
import { useEffect, useState } from "react";
import { AQUA } from "../../constants/tokens";
import { useActiveWeb3React } from "../../hooks/web3";
import { useMerkleDistributorContract } from "../../hooks/useContract";
import { calculateGasMargin } from "../../utils/calculateGasMargin";
import { useSingleCallResult } from "../multicall/hooks";
import { isAddress } from "../../utils";
import { useTransactionAdder } from "../transactions/hooks";

interface UserClaimData {
  index: number;
  amount: string;
  proof: string[];
  flags?: {
    isSOCKS: boolean;
    isLP: boolean;
    isUser: boolean;
  };
}

// parse distributorContract blob and detect if user has claim data
// null means we know it does not
export function useUserClaimData(
  account: string | null | undefined
): UserClaimData | null | undefined {
  const { chainId } = useActiveWeb3React();

  const key = `${chainId}:${account}`;
  const [claimInfo, setClaimInfo] = useState<{
    [key: string]: UserClaimData | null;
  }>({});

  return account && chainId ? claimInfo[key] : undefined;
}

// check if user is in blob and has not yet claimed AQUA
export function useUserHasAvailableClaim(
  account: string | null | undefined
): boolean {
  const userClaimData = useUserClaimData(account);
  const distributorContract = useMerkleDistributorContract();
  const isClaimedResult = useSingleCallResult(
    distributorContract,
    "isClaimed",
    [userClaimData?.index]
  );
  // user is in blob and contract marks as unclaimed
  return Boolean(
    userClaimData &&
      !isClaimedResult.loading &&
      isClaimedResult.result?.[0] === false
  );
}

export function useUserUnclaimedAmount(
  account: string | null | undefined
): CurrencyAmount<Token> | undefined {
  const { chainId } = useActiveWeb3React();
  const userClaimData = useUserClaimData(account);
  const canClaim = useUserHasAvailableClaim(account);

  const uni = chainId ? AQUA[chainId] : undefined;
  if (!uni) return undefined;
  if (!canClaim || !userClaimData) {
    return CurrencyAmount.fromRawAmount(uni, JSBI.BigInt(0));
  }
  return CurrencyAmount.fromRawAmount(uni, JSBI.BigInt(userClaimData.amount));
}

export function useClaimCallback(
  account: string | null | undefined
): {
  claimCallback: () => Promise<string>;
} {
  // get claim data for this account
  const { library, chainId } = useActiveWeb3React();
  const claimData = useUserClaimData(account);

  // used for popup summary
  const unclaimedAmount:
    | CurrencyAmount<Token>
    | undefined = useUserUnclaimedAmount(account);
  const addTransaction = useTransactionAdder();
  const distributorContract = useMerkleDistributorContract();

  const claimCallback = async function () {
    if (!claimData || !account || !library || !chainId || !distributorContract)
      return;

    const args = [claimData.index, account, claimData.amount, claimData.proof];

    return distributorContract.estimateGas["claim"](...args, {}).then(
      (estimatedGasLimit) => {
        return distributorContract
          .claim(...args, {
            value: null,
            gasLimit: calculateGasMargin(estimatedGasLimit),
          })
          .then((response: TransactionResponse) => {
            addTransaction(response, {
              summary: `Claimed ${unclaimedAmount?.toSignificant(4)} AQUA`,
              claim: { recipient: account },
            });
            return response.hash;
          });
      }
    );
  };

  return { claimCallback };
}
